<template>
  <div class="flex flex-column gap-3">


    <Accordion >
      <AccordionTab header="Фильтр">
        <div
            class="flex flex-wrap align-items-center justify-content-start gap-2"
        >
          <div class="flex flex-column flex-nowrap gap-2">
            <div>Лицевой счет</div>
            <InputText
                v-model="personAccount"
                placeholder="Лицевой счет"
                class="w-full md:w-14rem"
            />
          </div>
          <div class="flex flex-column flex-nowrap gap-2">
            <div>IP - адрес</div>
            <InputText
                v-model="ipAddress"
                placeholder="IP - адрес"
                class="w-full md:w-14rem"
            />
          </div>
          <div class="flex flex-column flex-nowrap gap-2">
            <div>Город</div>
            <Dropdown
                v-model="selectedCity"
                :options="spr.cityOptions.value"
                optionLabel="NAME"
                optionValue="ID"
                class="w-full md:w-14rem"
            />
          </div>

          <div class="flex flex-column flex-nowrap gap-2">
            <div>Статус</div>
            <Dropdown
                v-model="statusSelected"
                :options="spr.status.value"
                optionLabel="NAME"
                optionValue="ID"
                class="w-full md:w-14rem"
            />
          </div>



        </div>


        <div
            class="flex flex-wrap align-items-center justify-content-end gap-3"
        >
          <Button text label="Очистить" @click="clearFilter()" />
          <Button
              label="Показать"
              @click="searchEntity()"
              @keydown.enter="searchEntity()"
          />
        </div>

      </AccordionTab>
    </Accordion>







    <DataTable
        v-if="sp.getMetrics"
        lazy
      v-model:expandedRows="expandedRows"
      :sortOrder="-1"
      :value="sp.getMetrics"
      paginator
      :rows="10"
      :totalRecords="Number(spr.metricsCount.value)"
      :rowsPerPageOptions="[5, 10, 20, 50]"
      @page="onPage($event)"
      dataKey="ID"
      tableStyle="min-width: 60rem"
    >
      <Column expander style="width: 5rem" />
      <Column field="ABONENT">
        <template #header>
          <div>Лицевой счет</div>
        </template>
        <template #body="slotProps">
          {{ slotProps.data.ABONENT }}
        </template>
      </Column>
      <Column field="IP">
        <template #header> IP - адрес узла </template>
        <template #body="slotProps">
          {{ slotProps.data.IP }}
        </template>
      </Column>
      <Column field="PORT">
        <template #header> Порт узла </template>
        <template #body="slotProps">
          {{ slotProps.data.PORT }}
        </template>
      </Column>

      <Column field="DROP_METRIC">
        <template #header> Отклонение Метрик </template>
        <template #body="slotProps">
          {{ slotProps.data.DROP_METRIC }}
        </template>
      </Column>
      <Column field="abonentAddress">
        <template #header> Адрес подключения </template>
        <template #body="slotProps">
          {{ slotProps.data.abonentAddress }}
        </template>
      </Column>
      <Column sortable field="STATUS_ID">
        <template #header> Статус </template>
        <template #body="slotProps">
          <Dropdown
            v-model="slotProps.data.STATUS_ID"
            :options="sp.getStatus"
            optionLabel="NAME"
            option-value="ID"
            checkmark
            :highlightOnSelect="false"
            class="w-full md:w-14rem"
            @change="changeStatus(slotProps.data)"
          />
        </template>
      </Column>

      <Column field="DEAL_ID">
        <template #header>
          <span>Заявка</span>
        </template>
        <template #body="slotProps">
          <span
            @click="
              openDeal(slotProps.data.DEAL_ID > 0 ? slotProps.data.DEAL_ID : '')
            "
            >{{
              slotProps.data.DEAL_ID > 0 ? slotProps.data.DEAL_ID : ""
            }}</span
          >
        </template>
      </Column>

      <Column field="USER_NAME">
        <template #header>
          <span>Кто завел</span>
        </template>
        <template #body="slotProps">
          <span
              @click="
              openUser(slotProps.data.DEAL_CREATED_BY_ID > 0 ? slotProps.data.DEAL_CREATED_BY_ID : '')
            "
          >{{
              slotProps.data.USER_NAME  ? slotProps.data.USER_NAME : ""
            }}</span
          >
        </template>
      </Column>


      <Column style="width: 5rem">
        <template #header> </template>
        <template #body="slotProps">
          <div class="flex flex-wrap justify-content-end">
            <Button
              icon="pi pi-file-edit"
              size="small"
              text
              @click="CreateDeal(slotProps.data)"
            />
          </div>
        </template>
      </Column>

      <template #expansion="slotProps">
        <DataTable :value="slotProps.data.METRIC" size="small">
          <Column field="NAME">
            <template #header> Название метрики </template>
            <template #body="slotProps">
              <span>{{ slotProps.data.NAME }}</span>
            </template>
          </Column>

          <Column field="LAST_DATE">
            <template #header> Последняя проверка </template>
            <template #body="slotProps">
              <span>{{ slotProps.data.LAST_DATE }}</span>
            </template>
          </Column>
          <Column field="COUNT">
            <template #header> Колличество отклонений </template>
            <template #body="slotProps">
              <span>{{ slotProps.data.COUNT }}</span>
            </template>
          </Column>
          <Column field="LAST_METRIC">
            <template #header> Текущее значение </template>
            <template #body="slotProps">
              <span>{{ slotProps.data.LAST_METRIC }}</span>
            </template>
          </Column>
          <Column field="history">
            <template #header> История </template>
            <template #body="slotProps">
              <Button
                label="Посмотреть"
                link
                @click="
                  openHistory(
                    slotProps.data.ABONENT_ID,
                    slotProps.data.METRIC_ASUO,
                    slotProps.data.NAME
                  )
                "
              />
            </template>
          </Column>
        </DataTable>
      </template>
    </DataTable>

    <Dialog
      v-model:visible="hisDialog"
      :header="'Метрика' + ' ' + dialogHeader"
      :style="{ width: '50rem' }"
      :modal="true"
      :draggable="false"
    >
      <DataTable :value="sp.getUnitsTable" size="small">
        <Column field="DATE_CREATE">
          <template #header> Дата проверки </template>
          <template #body="slotProps">
            <span>{{ slotProps.data.DATE_CREATE }}</span>
          </template>
        </Column>
        <Column field="NEW_VALUE">
          <template #header> Текущее значение </template>
          <template #body="slotProps">
            <span>{{ slotProps.data.NEW_VALUE }}</span>
          </template>
        </Column>

        <Column field="NEW_VALUE">
          <template #header> Старое значение </template>
          <template #body="slotProps">
            <span>{{ slotProps.data.OLD_VALUE }}</span>
          </template>
        </Column>
        <Column field="DIFF_VALUE">
          <template #header> Разница </template>
          <template #body="slotProps">
            <span>{{
              slotProps.data.DIFF_VALUE ? slotProps.data.DIFF_VALUE : ""
            }}</span>
          </template>
        </Column>
      </DataTable>
    </Dialog>
  </div>
</template>

<script setup>
import { onMounted, ref } from "vue";
import { storeToRefs } from "pinia";
import { storeProactive } from "@/store/storeProactive";
import moment from "moment";

const sp = storeProactive();
const spr = storeToRefs(sp);

const expandedRows = ref([]);
const hisDialog = ref(false);
const dialogHeader = ref("");

const personAccount = ref();
const selectedCity = ref();
const statusSelected = ref();
const ipAddress = ref();
const clearFilter = () => {
  // datesCreate.value = null;
  // datesClosed.value = null;
  personAccount.value = null;
  selectedCity.value = null;
  statusSelected.value = null;
  ipAddress.value = null;

};
const openUser = (openUser, lk) => {
  if (openUser) {
    BX.SidePanel.Instance.open(
        "/company/personal/user/"+openUser+"/"
    );
  } else {
    toast.add({
      severity: "info",
      summary: "В базе не найден Пользователь:",
      detail: lk,
      group: "tr",
      life: 3000,
    });
  }
};
const searchEntity = async () => {
  var filter = [];
  // if (datesCreate.value) {
  //   filter.push({
  //     ">=DATE_CREATE":
  //         moment(datesCreate.value[0]).format("DD.MM.YYYY") + " 00:00:00",
  //   });
  //   filter.push({
  //     "<=DATE_CREATE":
  //         moment(datesCreate.value[1]).format("DD.MM.YYYY") + " 23:59:59",
  //   });
  // }
  // if (datesClosed.value) {
  //   filter.push({
  //     ">=DATE_CLOSED":
  //         moment(datesClosed.value[0]).format("DD.MM.YYYY") + " 00:00:00",
  //   });
  //   filter.push({
  //     "<=DATE_CLOSED":
  //         moment(datesClosed.value[1]).format("DD.MM.YYYY") + " 23:59:59",
  //   });
  // }

  if (selectedCity.value) {
    filter.push({ "LK.UF_CRM_5_CITY_LIST": selectedCity.value });
  }

  if (personAccount.value) {
    filter.push({ "LK.UF_CRM_5_LS_NUMBER": personAccount.value });
  }

  if (statusSelected.value) {
    filter.push({ STATUS_ID: statusSelected.value });
  }
  if (ipAddress.value) {
    filter.push({ IP: ipAddress.value });
  }



  await sp.loadMainTable(filter);
};

const openHistory = async (abonentId, metric, name) => {
  hisDialog.value = true;
  dialogHeader.value = name;

  await sp.loadUnits(abonentId, metric);
};
const CreateDeal = async (data) => {
        console.log(data)

      const dealId =  await sp.createDeal(data.LK_ID,data.ID);
        data.DEAL_ID = dealId
          BX.SidePanel.Instance.open('/crm/deal/details/'+dealId+'/', {width: 1480});
  console.log(dealId)
  //
};

const openDeal = (ID) => {
  if(ID > 0) {
       BX.SidePanel.Instance.open('/crm/deal/details/' + ID + '/', {width: 1480});
  }
};
const changeStatus = async (data) => {
  const upd = await new Promise((resolve) => {
    BX.rest.callMethod(
      "mwsproactiverest.abonen.update",
      { ID: data.ID, fields: data },
      (res) => {
        resolve(res.data());
      }
    );
  });

};
const onPage = async (event) => {
  var filter = [];
  // if (datesCreate.value) {
  //   filter.push({
  //     ">=DATE_CREATE":
  //         moment(datesCreate.value[0]).format("DD.MM.YYYY") + " 00:00:00",
  //   });
  //   filter.push({
  //     "<=DATE_CREATE":
  //         moment(datesCreate.value[1]).format("DD.MM.YYYY") + " 23:59:59",
  //   });
  // }
  // if (datesClosed.value) {
  //   filter.push({
  //     ">=DATE_CLOSED":
  //         moment(datesClosed.value[0]).format("DD.MM.YYYY") + " 00:00:00",
  //   });
  //   filter.push({
  //     "<=DATE_CLOSED":
  //         moment(datesClosed.value[1]).format("DD.MM.YYYY") + " 23:59:59",
  //   });
  // }

  if (selectedCity.value) {
    filter.push({ "LK.UF_CRM_5_CITY_LIST": selectedCity.value });
  }

  if (personAccount.value) {
    filter.push({ "LK.UF_CRM_5_LS_NUMBER": personAccount.value });
  }

  if (statusSelected.value) {
    filter.push({ STATUS_ID: statusSelected.value });
  }
  if (ipAddress.value) {
    filter.push({ IP: ipAddress.value });
  }
  var limited ={limit:event.rows,
    page:event.page,}


  await sp.loadMainTable(filter,limited);
};

onMounted(async () => {
  await sp.loadStatusProactive();
  await sp.loadMainTable();
});
</script>

<style scoped></style>
